<template>
  <div class="home pa-6">
    <v-text-field class="pa-4"
            @click:append="addTask"
            @keyup.enter="addTask"
            v-model="newTask"
            outlined
            label="Add New Task"
            append-icon="mdi-plus"
            hide-details
            clearable
          ></v-text-field>
    <v-list
      subheader
      flat
    >
      <v-subheader>All To-Dos</v-subheader>
        <v-list-item v-for="task in tasks" :key="task.id" @click="completedTask(task.id)" :class="{ 'green accent-1' : task.completed }">
          <template>
            <v-list-item-action>
              <v-checkbox
                :input-value="task.completed"
                color="primary"
              ></v-checkbox>
            </v-list-item-action>

            <v-list-item-content>
              <v-list-item-title>{{ task.title }}</v-list-item-title>
            </v-list-item-content>

            <v-list-item-action>
              <v-btn @click.stop="deleteTask(task.id)" icon>
                <v-icon color="red lighten-1">mdi-delete</v-icon>
              </v-btn>
            </v-list-item-action>
          </template>
        </v-list-item>
    </v-list>
</div>

</template>

<script>

export default {
  name: 'Home',
  methods: {
    addTask() {
      let newTask = {
        id: Date.now(),
        title: this.newTask,
        done: false
      }
      this.tasks.push(newTask)
      this.newTask = ""
    },

    completedTask(id){
      let task = this.tasks.filter(task => task.id === id)[0]
      task.completed = !task.completed
    },
    deleteTask(id){
      this.tasks = this.tasks.filter( task => task.id !== id)
    }
  },
  data() {
    return {
      newTask: "",

      tasks: [
        {
          id: 1,
          title: 'Wake Up!',
          completed:false
        },
        {
          id: 2,
          title: 'Learn to code.',
          completed:false
        },
        {
          id: 3,
          title: 'Build Projects.',
          completed:false
        },
      ]
    }
  }
}
</script>
